// Override Boostrap variables
@import 'bootstrap-variables';
@import '../scss/bootstrap';
// Override core variables
@import 'core-variables';

@import '../scss/shared';

.login-logo {
  margin: 1rem auto;
  margin-top: 0 !important;
  margin-bottom: 1.5rem !important;
}

.registration-page {
  padding-bottom: 0.75rem;

  .text-white {
    color: $body-color !important;
  }
}

.registration-box {
  .registration-logo {
    width: 180px;
  }
}

.kyc-wizard {
  .login-logo {
    width: 180px;
    margin: 1.5rem auto;
  }

  h2.text-primary {
    color: theme-color('secondary') !important;
  }

  .text-white {
    color: $body-color !important;
  }
}

.btn-primary:not(.loading),
.btn-secondary:not(.loading) {
  color: white !important;
}

.sidebar {
  @media only screen and (min-width: map-get($grid-breakpoints, lg)) and (max-width: map-get($grid-breakpoints, xl)) {
    .navbar .navbar-brand {
      background-size: 40px;
    }
  }
}

.shadow {
  box-shadow: 0 0.25rem 1rem rgba(0, 0, 0, 0.075) !important;
}

.cc-logo {
  height: 38px;
  margin-top: -5px;
  margin-bottom: 20px;
}

kyc-wizard .login-logo {
  width: 180px;
  margin-top: 1.5rem !important;
}

.sidebar .navbar .navbar-brand {
  background-size: 170px auto;
}

@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .sidebar .navbar .navbar-brand {
    background-size: 58px;
  }
}

.sidebar .navbar .navbar-brand {
  background-color: $gray-800 !important;
}

.sidebar-nav .nav-link.active,
.sidebar-nav .nav-item.active > .nav-link {
  background-color: theme-color('primary');
}

.sidebar-nav .nav-link.active svg,
.sidebar-nav .nav-link:hover svg,
.sidebar-nav .nav-item.active > .nav-link svg,
.sidebar-nav .nav-item .nav .nav-link.active svg,
.sidebar-nav .nav-item .nav .nav-link:hover svg {
  color: theme-color('secondary');
}

.sidebar-profile {
  background-color: theme-color('primary');

  .text-primary {
    color: theme-color('secondary') !important;
  }

  .avatar.avatar-border .img-avatar {
    border: 4px solid theme-color('secondary') !important;
  }

  .avatar .avatar-status {
    border: 2px solid theme-color('primary') !important;
  }
}

.sidebar-profile .profile:hover,
.sidebar-profile .profile.open {
  background-color: darken(theme-color('primary'), 5%) !important;
}

.sidebar-profile .list-group-item {
  background-color: darken(theme-color('primary'), 8%) !important;
  color: theme-color('secondary') !important;

  &:hover {
    background-color: darken(theme-color('primary'), 10%) !important;
  }
}

.quickbar {
  background: darken($body-bg, 5%);
}

.custom-control-input:checked ~ .custom-control-label.text-secondary::before {
  border-color: theme-color('secondary') !important;
  background-color: theme-color('secondary') !important;
}

.custom-control-input:focus ~ .custom-control-label.text-secondary::before {
  box-shadow: 0 0 0 0.2rem rgb(226 128 86 / 25%) !important;
}

.bg-custom {
  background: url(img/backgrounds/bg.png) no-repeat center center fixed;
  background-size: cover;
  background-color: #64c2e6 !important;
}

.login-page {
  .text-white {
    color: $body-color !important;
  }
}
